@import "../../../styles/vars";
@import "../../../styles/mixins";

.subscribePopup {

  &__wrapper {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 99999;
    max-width: 1402px;
    width: calc(100% - 20px);
    margin: 0 auto;
    background: linear-gradient(90deg, #00D8FF 0%, #22A3EC 94.72%, #2A97E8 116.61%, #2A97E8 116.61%);
    border-radius: 20px;
    padding: 117px 16px 117px 67px;
    max-height: calc(100vh - 20px);
    overflow: hidden;

    @include xxlScreenMedia {
      padding: 107px 30px 107px 64px;
      max-width: 1224px;
    }

    @include xxlScreenMedia {
      padding: 107px 10px 97px 62px;
      max-width: 1087px;
    }


    @include lgScreenMedia {
      padding: 80px 38px 80px 22px;
      gap: 11px;
      max-width: 853px;
    }

    @include smScreenMedia {
      padding: 32px 5px 35px 5px;
      max-width: 320px;
      border-radius: 16px;
    }
  }

  &__main {
    @include flexCol(space-between);
    gap: 8px;
  }

  &__crossBtn {
    cursor: pointer;
    border: none;
    background: transparent;
    position: absolute;
    top: 32px;
    right: 39px;

    @include lgScreenMedia {
      top: 26px;
      right: 28px;
    }

    @include smScreenMedia {
      top: 10px;
      right: 10px;
    }
  }

  &__crossIcon {
    @include svg(36px, $color-white);

    @include lgScreenMedia {
      width: 28px;
      height: 28px;
    }

    @include smScreenMedia {
      width: 14px;
      height: 14px;
    }
  }

  &__robotBlock {
    @include flexCol();
    flex-direction: column;
    padding-left: 57px;
    padding-right: 24px;
    max-width: 520px;

    @include xxlScreenMedia {
      max-width: 440px;
    }

    @include xlScreenMedia {
      padding-left: 42px;
      padding-right: 24px;
      max-width: 380px;
    }

    @include lgScreenMedia {
      padding-right: 25px;
      padding-left: 30px;
      max-width: 303px;
    }

    @media (max-width: 850px) {
      max-width: 245px;
      padding-right: 26px;
      padding-left: 18px;
    }

    @include mdScreenMedia {
      max-width: 150px;
      padding-right: 9px;
      padding-left: 19px;
    }

    @include smScreenMedia {
      max-width: 97px;
      padding-right: 7px;
      padding-left: 10px;
    }
  }

  &__robotHeadImg {
    max-width: 100%;
    margin-bottom: -1px;
  }

  &__robotBody {
    position: relative;
    @include flexCol();
    width: 100%;
  }

  &__robotBodyInner {
    position: relative;
    margin: 0 auto;
    width: 80%;
  }

  &__robotBodyImg {
    width: 100%;
  }

  &__robotHandImg {
    position: absolute;
    top: 50%;
    width: 50%;

    &_left {
      left: 0;
      transform-origin: right;
      animation: handMove .8s linear infinite;

      @include mdScreenMedia {
        animation: handMoveMob .8s linear infinite;
      }
    }

    &_right {
      right: 0;
      transform: translate(calc(100% - 55px), -235%) rotate(45deg);
      transform-origin: left;

      @include xxlScreenMedia {
        transform: translate(calc(100% - 55px), -235%) rotate(45deg);
      }

      @include xlScreenMedia {
        transform: translate(calc(100% - 40px), -225%) rotate(45deg)
      }

      @include lgScreenMedia {
        transform: translate(calc(100% - 32px), - 225%) rotate(45deg);
      }

      @media (max-width: 850px) {
        transform: translate(calc(100% - 15px), - 225%) rotate(45deg);
      }

      @include smScreenMedia {
        transform: translate(calc(100% - 12px), -233%) rotate(45deg);
      }
    }
  }

  &__textBlock {
    max-width: 638px;
    width: 100%;
    position: relative;

    @include xxlScreenMedia {
      max-width: 607px;
    }
    @include xlScreenMedia {
      max-width: 522px;
    }

    @include lgScreenMedia {
      max-width: 405px;
    }

    @media (max-width: 850px) {
      max-width: 350px;
    }

    @include mdScreenMedia {
      max-width: 300px;
    }

    @include smScreenMedia {
      max-width: 180px;
    }
  }

  &__formBlock {
    background-image: url("../../../assets/images/subscribeModal/subscribeModalBg.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    @include flexCol();
    flex-direction: column;
    padding: 220px 55px 130px 20px;

    @include xlScreenMedia {
      background-image: url("../../../assets/images/subscribeModal/subscribeModalBgTab.png");
      padding: 90px 34px 160px 0;
    }

    @include lgScreenMedia {
      padding-right: 0;
      padding-bottom: 110px;
    }

    @media (max-width: 850px) {
      padding-bottom: 85px;
    }

    @include mdScreenMedia {
      background-image: url("../../../assets/images/subscribeModal/subscribeModalBgMob.png");
      padding-bottom: 70px;
      padding-top: 50px;
    }

    @include smScreenMedia {
      padding-bottom: 30px;
      padding-top: 26px;
      background-size: 90% 100%;
    }
  }

  &__titleText {
    @include setFont(36px, 500);
    margin-bottom: 20px;
    color: $color-white;

    @include xxlScreenMedia {
      font-size: 34px;
    }

    @include xlScreenMedia {
      font-size: 30px;
    }

    @include lgScreenMedia {
      font-size: 25px;
    }

    @media (max-width: 850px) {
      font-size: 22px;
    }

    @include mdScreenMedia {
      font-size: 18px;
    }

    @include smScreenMedia {
      margin-bottom: 8px;
      font-size: 14px;
    }
  }

  &__contentText {
    @include setFont(22px, 500);
    margin-bottom: 36px;
    color: $color-white;

    @include xlScreenMedia {
      font-size: 18px;
    }

    @include lgScreenMedia {
      font-size: 16px;
      margin-bottom: 20px;
    }

    @include smScreenMedia {
      font-size: 12px;
      margin-bottom: 0;
    }
  }

  &__form {
    &_desk {
      @include mdScreenMedia {
        display: none !important;
      }
    }

    &_mob {
      margin-top: 20px;

      @media (min-width: 769px) {
        display: none !important;
      }

      @include smScreenMedia {
        margin-left: 15px;
        margin-right: 15px;
        max-width: calc(100% - 30px);
      }
    }
  }

  &__thankBlock {
    @include flexCol();
    flex-direction: column;
    gap: 43px;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    @include lgScreenMedia {
      gap: 30px;
    }
  }

  &__thankBlockTitle {
    @include setFont(36px, 500);
    color: $color-white;

    @include xxlScreenMedia {
      font-size: 34px;
    }

    @include xlScreenMedia {
      font-size: 30px;
    }

    @include lgScreenMedia {
      font-size: 25px;
    }

    @media (max-width: 850px) {
      font-size: 22px;
    }

    @include mdScreenMedia {
      font-size: 18px;
    }

    @include smScreenMedia {
      font-size: 14px;
    }
  }
}


@keyframes handMove {
  0%, 100% {
    transform: translate(calc(-100% + 24px), -130%) rotate(35deg);
  }
  50% {
    transform: translate(calc(-100% + 24px), -130%) rotate(55deg);
  }
}

@keyframes handMoveMob {
  0%, 100% {
    transform: translate(calc(-100% + 10px), -130%) rotate(35deg);
  }
  50% {
    transform: translate(calc(-100% + 10px), -130%) rotate(55deg);
  }
}